(function() {
  'use strict';

  angular
    .module('teambookWww')
    .run(runBlock);

  /** @ngInject */
  function runBlock($location,$http,user,teambookConfig,toastr) {

    $http.get(teambookConfig.apiBaseUrl + "/health").then(function(){}, function () {
      toastr.error("无法连接接口服务器,请联系管理员!");
    });
    $location.pre = angular.copy($location.path());

    if(angular.isUndefined(user.getUser())){
      $location.path("/login");
    }

  }

})();

(function () {
  'use strict';

  angular
    .module('teambookWww')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      });

    $stateProvider
      .state('company', {
        url: '/company',
        templateUrl: 'app/company/company.html',
        controller: 'CompanyController',
        controllerAs: 'company'
      });

    $stateProvider
      .state('login', {
        url: '/login',
        templateUrl: 'app/user/login.html',
        controller: 'LoginController',
        controllerAs: 'login'
      });

    $stateProvider
      .state('logout', {
        url: '/logout',
        controller: 'LogoutController',
        controllerAs: 'logout'
      });



    $urlRouterProvider.otherwise('/');
  }

})();

